.hero_home {
  width: 90%;
  max-width: 1247px;
  margin: auto;
  margin-top: 8rem;
  /* border: 1px solid orange; */
  margin-bottom: 100px;
  align-items: center;
  padding: 5.8rem 0;
}

.hero_home_main {
  display: grid;
  align-items: center;
  justify-content: space-between;
  grid-template-columns: repeat(2, 1fr);
  width: 90%;
  /* border: 1px solid red; */
  margin: auto;
  gap: 1%;
}
.hero_home_intro {
  /* border: 1px solid rgb(188, 144, 144); */
}

.intro_name {
  color: #00ff00;
  font-size: 1.2rem;
  margin-bottom: -1rem;
}
.intro_name_b {
  color: #00ff00;
  font-size: 2.5rem;
  margin-bottom: -1rem;
}
.intro_title {
  color: #e0e7ed;
  font-size: 1.5em;
}
.about_me_btn {
  background-color: #00ff00;
  width: 190px;
  height: 50px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  margin-top: -10px;
}
.about_me_btn > p {
  font-size: medium;
}
.profile_picture {
  margin: auto;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile_picture > img {
  width: 50%;
}

/* MEDIA QUERY */
@media screen and (min-width: 1025px) {
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .hero_home_main {
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }
  .about_me_btn {
    margin: auto;
  }
  .hero_home_intro {
    align-items: center;
  }
  .profile_picture > div:nth-child(1) > img {
    width: 20%;
  }
}
@media screen and (max-width: 481px) {
  .hero_home_main {
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }
  .about_me_btn {
    margin: auto;
  }
  .hero_home {
    margin-top: 1rem;
    margin-bottom: 100px;
    align-items: center;
    padding: 5.8rem 0;
  }
}
