/* NAV B */
.navbar_b_main_div {
  margin: auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  padding: calc(1em) 0;
}
.navB_logo {
  width: calc(2em);
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 1px solid red; */
}
.navB_ham {
  width: calc(2em);
  display: flex;
  align-items: center;
  justify-content: center;
}
/* ELLIPSIS */
.ellipsis {
  width: 80%;
  height: 50%;
  border-radius: 5px;
  cursor: pointer;
}
.ellipsis:hover {
  background-color: #b1efb113;
}
