.about_section {
  width: 100%;
  background-color: #00ff00;
}

.about_section_inner {
  width: 90%;
  margin: auto;
  max-width: 1247px;
  display: flex;
  padding: 2% 0;
}
.about_heading {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about_heading > h1 {
  font-size: calc(2.7rem);
}
.about_description {
  width: 68%;
  display: flex;
  flex-direction: column;
}
.about_desc_desc {
  text-align: justify;
  font-size: calc(1rem);
}
.about_desc_icons {
  display: flex;
  align-items: center;
  gap: 2%;
  font-size: 1.2rem;
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .about_section_inner {
    flex-direction: column;
  }
  .about_heading {
    width: 100%;
    margin-bottom: -2em;
  }
  .about_description {
    width: 100%;
  }
}

@media screen and (max-width: 481px) {
  .about_section_inner {
    flex-direction: column;
  }
  .about_heading {
    width: 100%;
    margin-top: -1.5em;
    margin-bottom: -2em;
  }
  .about_description {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
