.logos_container {
  /* border: 1px solid red; */
  width: 90%;
  max-width: 1247px;
  margin: auto;
  margin-top: 100px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1em;
  margin-top: 10em;
  margin-bottom: 10em;
}
.logos_container > * {
  cursor: default;
}
.logo_card {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #00ff00;
  padding: 1em 0;
  border-radius: 0.5em;
  box-shadow: rgba(0.2, 0.2, 0.2, 0.4) 2px 2px 4px,
    rgba(0.2, 0.2, 0.2, 0.3) 0px 7px 13px -3px,
    rgba(0.1, 0.1, 0.1, 0.2) 0px -3px 0px inset;
  transition: 0.5s;
}
.logo_card:hover {
  background-color: #00ff00;
  color: #000000;
  transition: 0.5s;
}
.logos_container > div {
  border: 1px solid #00ff0000;
  background-color: #1e2032;
}
.logo_icon {
  font-size: calc(4rem);
}
.logo_name {
  font-size: calc(0.9rem);
}
/*  */
@media screen and (max-width: 1024px) and (min-width: 871px) {
  .logos_container {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media screen and (max-width: 871px) and (min-width: 634px) {
  .logos_container {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (max-width: 634px) and (min-width: 481px) {
  .logos_container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 481px) {
  .logos_container {
    grid-template-columns: repeat(2, 1fr);
  }
}
